import { Fragment, useEffect, useState } from "react";
import "../authentication/authentication.css";
import { useDispatch } from "react-redux";
import { axiosPost } from "../../services/axiosService";
import { Link } from "react-router-dom";
import { AiChatBotShortLogo } from "../../assets/AiBotAssets";
import environment from "../../constants/Environment";
import { userSlice } from "../../redux/User";

function ForgotPassword() {
  const dispatch = useDispatch();
  const [email, setEmail] = useState<any>();
  const [emailValid, setEmailValid] = useState<any>();
  const [emailExists, setEmailExists] = useState<any>(true);
  const [loader, setLoader] = useState<any>(false);
  const [verify, setVerify] = useState<any>();
  const [emailEmpty, setEmailEmpty] = useState<boolean>(false);

  useEffect(() => {
    setTimeout(() => {
      setEmailExists(true);
    }, 5000);
  }, [emailValid, emailExists]);
  const validateEmail = (email: any) => {
    var validator = new RegExp(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
    if (email.target.value.length > 0) {
      if (validator.test(email.target.value)) {
        setEmailValid(true);
        setEmail(email.target.value.toLowerCase());
      } else {
        setEmailValid(false);
        setEmail("");
      }
    } else {
      setEmailValid(undefined);
    }

    if (email.target.value === "") {
      setEmailEmpty(true);
    } else {
      setEmailEmpty(false);
    }
  };

  const handleVerificationLink = (e) => {
    console.log(e);
    if (e.type == "click" || e.key == "Enter") {
      setLoader(true);
      if (email) {
        const body = {
          email: email,
        };
        axiosPost(`/users/forgotpassword`, body).then((response: any) => {
          if (response.data.success === true) {
            setVerify(true);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "success",
                message: `Email sent successfully`,
              })
            );
          } else {
            setEmailExists(false);
          }
          setLoader(false);
        });
      }
    }
  };

  return (
    <Fragment>
      <div className="main-cont-fp">
        <div className="omni-logo">
          {environment().isAI ? (
            <AiChatBotShortLogo />
          ) : (
            <svg width="54" height="54" viewBox="0 0 68 68" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M45.3741 30.4054C47.512 29.4679 50.0516 30.4409 50.5457 32.7226C51.2867 36.1448 51.1705 39.7205 50.176 43.1238C48.7834 47.8892 45.7576 52.0123 41.6292 54.7701C37.5008 57.5279 32.5334 58.7443 27.5979 58.206C22.6624 57.6677 18.0739 55.4091 14.6371 51.8262C11.2003 48.2433 9.13443 43.5649 8.80187 38.6113C8.46931 33.6577 9.89124 28.7451 12.8183 24.735C15.7454 20.7248 19.9908 17.8732 24.81 16.68C28.2517 15.8279 31.8292 15.8605 35.2176 16.7432C37.4767 17.3317 38.3433 19.9096 37.3177 22.0067V22.0067C36.292 24.1038 33.7439 24.891 31.4266 24.6081C29.9056 24.4223 28.3519 24.5123 26.8418 24.8862C23.9502 25.6021 21.403 27.3131 19.6468 29.7192C17.8905 32.1253 17.0373 35.0728 17.2369 38.045C17.4364 41.0171 18.6759 43.8242 20.738 45.9739C22.8001 48.1237 25.5532 49.4788 28.5145 49.8018C31.4758 50.1248 34.4563 49.3949 36.9333 47.7403C39.4103 46.0856 41.2258 43.6118 42.0613 40.7525C42.4977 39.2592 42.6523 37.7107 42.5299 36.1832C42.3436 33.8562 43.2361 31.3429 45.3741 30.4054V30.4054Z"
                fill="#FB5F66"
              ></path>
              <path
                d="M53.1185 23.6639C52.3799 24.2422 50.1861 23.4112 47.814 22.3194C45.9231 23.0419 44.7003 22.6322 47.2581 25.3863C49.1221 27.3934 43.7167 27.1529 40.5963 30.2732C36.5793 34.4384 38.3334 43.7882 33.3978 35.9912C31.538 33.0532 34.1797 42.0226 32.9266 42.3874C31.2648 42.6672 31.4684 40.6416 30.8727 35.8983C30.5888 33.6373 30.2527 35.6886 29.8491 38.1711C31.5761 42.301 31.0061 39.5987 29.7744 41.0952L29.4155 40.6893C29.1159 42.237 28.7841 43.3955 28.4158 43.0272C27.5652 42.1765 26.7057 37.7753 25.551 36.3197L25.3955 36.144C25.4479 36.1964 25.4997 36.2552 25.551 36.3197L29.4155 40.6893C29.5682 39.9008 29.7125 39.0113 29.8491 38.1711C29.3228 36.9127 28.5833 35.0199 27.5843 32.1807C26.5209 27.9698 33.7239 30.3348 36.6549 27.996C39.586 25.6572 40.7038 22.6146 40.2445 19.5203C40.3024 18.4608 44.2585 20.6829 47.814 22.3194C48.4192 22.0881 49.0928 21.7408 49.7329 21.1367C50.1888 20.7063 50.5495 20.2429 50.7828 19.7461L46.7626 13.0023C50.7998 15.6984 51.6303 17.9421 50.7828 19.7461L53.1185 23.6639Z"
                fill="#444444"
              ></path>
              <circle cx="47.0261" cy="20.4576" r="7.51467" transform="rotate(-135 47.0261 20.4576)" fill="#444444"></circle>
              <circle cx="30.155" cy="37.3282" r="9.0176" transform="rotate(-135 30.155 37.3282)" fill="#444444"></circle>
            </svg>
          )}
          <h1>{environment().isAI ? "Chatbot Builder" : "Omniengage"}</h1>
        </div>
        {verify ? (
          <div className="fg_pass_cont">
            <div className="back-to-lgin">
              <Link to="/login">
                <div className="bck-lg-in">
                  <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                      d="M12.998 6.0018H2.97852L6.27494 2.70086C6.36885 2.60759 6.44339 2.49662 6.49426 2.37435C6.54513 2.25209 6.57132 2.12095 6.57132 1.9885C6.57132 1.85605 6.54513 1.72491 6.49426 1.60264C6.44339 1.48038 6.36885 1.36941 6.27494 1.27613C6.08721 1.08926 5.83327 0.984375 5.56856 0.984375C5.30386 0.984375 5.04992 1.08926 4.86219 1.27613L0.563809 5.59044C0.187487 5.96503 -0.0251066 6.47379 -0.0273438 7.00513C-0.0224677 7.53299 0.189924 8.03766 0.563809 8.40979L4.86219 12.7241C4.95561 12.817 5.06638 12.8905 5.18819 12.9406C5.31 12.9906 5.44045 13.0161 5.57211 13.0156C5.70376 13.0152 5.83403 12.9887 5.95548 12.9378C6.07693 12.887 6.18719 12.8126 6.27995 12.7191C6.37271 12.6255 6.44617 12.5146 6.49612 12.3926C6.54607 12.2706 6.57154 12.14 6.57107 12.0082C6.57061 11.8764 6.54422 11.7459 6.49341 11.6243C6.4426 11.5027 6.36836 11.3923 6.27494 11.2994L2.97852 8.00846H12.998C13.2638 8.00846 13.5186 7.90275 13.7065 7.71459C13.8944 7.52643 14 7.27123 14 7.00513C14 6.73903 13.8944 6.48383 13.7065 6.29567C13.5186 6.10751 13.2638 6.0018 12.998 6.0018Z"
                      fill="#FB5F66"
                    />
                  </svg>
                </div>
              </Link>
              <span>Back to Login</span>
            </div>
            <div className="fg-pass-form fg-pd-pg-2">
              <div className="fg-logo">
                <svg width="100" height="100" viewBox="0 0 100 100" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <rect width="100" height="100" rx="50" fill="#FB5F66" fillOpacity="0.1" />
                  <path
                    d="M34 41.9998L50 51.9998L66 41.9998L50 31.9998L34 41.9998ZM70 41.9998V61.9998C70 63.0607 69.5786 64.0781 68.8284 64.8282C68.0783 65.5784 67.0609 65.9998 66 65.9998H34C32.9391 65.9998 31.9217 65.5784 31.1716 64.8282C30.4214 64.0781 30 63.0607 30 61.9998V41.9998C30 40.5398 30.78 39.2798 31.94 38.5798L50 27.2798L68.06 38.5798C69.22 39.2798 70 40.5398 70 41.9998Z"
                    fill="#FB5F66"
                  />
                </svg>
              </div>
              <h2>Check your email</h2>
              <p>We have sent you password reset instructions on your email</p>
              <span className="email-fg">{email}</span>

              <div className="resend">
                <p>Didnâ€™t receive the email?</p>
                <span className="resend-link" onClick={handleVerificationLink}>
                  {loader ? "resending..." : "click here to resend"}
                </span>
              </div>
            </div>
          </div>
        ) : (
          <div className="fg_pass_cont">
            <div className="back-to-lgin">
              <Link to="/login">
                <div className="bck-lg-in">
                  <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                      d="M12.998 6.0018H2.97852L6.27494 2.70086C6.36885 2.60759 6.44339 2.49662 6.49426 2.37435C6.54513 2.25209 6.57132 2.12095 6.57132 1.9885C6.57132 1.85605 6.54513 1.72491 6.49426 1.60264C6.44339 1.48038 6.36885 1.36941 6.27494 1.27613C6.08721 1.08926 5.83327 0.984375 5.56856 0.984375C5.30386 0.984375 5.04992 1.08926 4.86219 1.27613L0.563809 5.59044C0.187487 5.96503 -0.0251066 6.47379 -0.0273438 7.00513C-0.0224677 7.53299 0.189924 8.03766 0.563809 8.40979L4.86219 12.7241C4.95561 12.817 5.06638 12.8905 5.18819 12.9406C5.31 12.9906 5.44045 13.0161 5.57211 13.0156C5.70376 13.0152 5.83403 12.9887 5.95548 12.9378C6.07693 12.887 6.18719 12.8126 6.27995 12.7191C6.37271 12.6255 6.44617 12.5146 6.49612 12.3926C6.54607 12.2706 6.57154 12.14 6.57107 12.0082C6.57061 11.8764 6.54422 11.7459 6.49341 11.6243C6.4426 11.5027 6.36836 11.3923 6.27494 11.2994L2.97852 8.00846H12.998C13.2638 8.00846 13.5186 7.90275 13.7065 7.71459C13.8944 7.52643 14 7.27123 14 7.00513C14 6.73903 13.8944 6.48383 13.7065 6.29567C13.5186 6.10751 13.2638 6.0018 12.998 6.0018Z"
                      fill="#FB5F66"
                    />
                  </svg>
                </div>
              </Link>
              <span>Back to Login</span>
            </div>
            <div className="fg-pass-form" onKeyDown={(e) => handleVerificationLink(e)}>
              <div className="fg-logo">
                <img src="https://res.cloudinary.com/dnag1wvx8/image/upload/v1659506322/Lock_1_gcuamb.svg" alt="" />
              </div>
              <h2>Forgot Password?</h2>
              <p>Dont worry we will help you get your account back</p>

              <div className="email-input">
                <label htmlFor="Email">Email</label>
                <div className="em-input-bx">
                  <div className="icn-bx-mail">
                    <svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path
                        d="M10.2917 0.541748H2.70833C1.9903 0.542608 1.30193 0.828226 0.794202 1.33595C0.286478 1.84368 0.00086009 2.53205 0 3.25008L0 9.75008C0.00086009 10.4681 0.286478 11.1565 0.794202 11.6642C1.30193 12.1719 1.9903 12.4576 2.70833 12.4584H10.2917C11.0097 12.4576 11.6981 12.1719 12.2058 11.6642C12.7135 11.1565 12.9991 10.4681 13 9.75008V3.25008C12.9991 2.53205 12.7135 1.84368 12.2058 1.33595C11.6981 0.828226 11.0097 0.542608 10.2917 0.541748ZM2.70833 1.62508H10.2917C10.616 1.62572 10.9327 1.7234 11.2011 1.90556C11.4695 2.08772 11.6772 2.34601 11.7975 2.64721L7.64942 6.79583C7.34415 7.09988 6.93085 7.27059 6.5 7.27059C6.06915 7.27059 5.65585 7.09988 5.35058 6.79583L1.2025 2.64721C1.32283 2.34601 1.53054 2.08772 1.7989 1.90556C2.06726 1.7234 2.38399 1.62572 2.70833 1.62508ZM10.2917 11.3751H2.70833C2.27736 11.3751 1.86403 11.2039 1.55928 10.8991C1.25454 10.5944 1.08333 10.1811 1.08333 9.75008V4.06258L4.58467 7.56175C5.09309 8.06889 5.78189 8.35369 6.5 8.35369C7.21811 8.35369 7.90691 8.06889 8.41533 7.56175L11.9167 4.06258V9.75008C11.9167 10.1811 11.7455 10.5944 11.4407 10.8991C11.136 11.2039 10.7226 11.3751 10.2917 11.3751Z"
                        fill="black"
                        fillOpacity="0.3"
                      />
                    </svg>
                  </div>
                  <input type="email" placeholder="Enter your work email address" onChange={validateEmail} />
                </div>
                {emailEmpty ? (
                  <span className="invalid-email-fg">
                    <svg width="11" height="11" viewBox="0 0 11 11" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path
                        d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09602 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099Z"
                        fill="#E31E26"
                      />
                    </svg>
                    Field can not be left blank
                  </span>
                ) : (
                  emailValid === false && (
                    <span className="invalid-email-fg">
                      <svg width="11" height="11" viewBox="0 0 11 11" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path
                          d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09602 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099Z"
                          fill="#E31E26"
                        />
                      </svg>
                      Invalid Email
                    </span>
                  )
                )}
                {emailExists !== undefined && !emailExists && (
                  <span className="invalid-email-fg">
                    <svg width="11" height="11" viewBox="0 0 11 11" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path
                        d="M10.655 6.2345L7.98647 1.4752C6.89991 -0.462669 4.10224 -0.463509 3.01527 1.4752L0.346758 6.2345C-0.657973 8.0266 0.641496 10.2392 2.69258 10.2392H8.30915C10.3812 10.2392 11.6489 8.00721 10.655 6.2345ZM8.30915 9.3802H2.69258C1.28586 9.3802 0.418764 7.86253 1.09602 6.65462L3.76454 1.89532C4.52362 0.541473 6.47794 0.541138 7.2372 1.89532L9.90571 6.65462C10.5837 7.86378 9.71453 9.3802 8.30915 9.3802ZM5.94663 3.01025V5.5444C5.94663 5.78165 5.75436 5.97392 5.51711 5.97392C5.27994 5.97392 5.08759 5.78165 5.08759 5.5444V3.01025C5.08759 2.77308 5.27994 2.58073 5.51711 2.58073C5.75436 2.58073 5.94663 2.77308 5.94663 3.01025ZM5.93043 7.24099V7.66338C5.93043 7.90054 5.73808 8.09281 5.50091 8.09281C5.26366 8.09281 5.07139 7.90054 5.07139 7.66338V7.24099C5.07139 7.00374 5.26366 6.81147 5.50091 6.81147C5.73808 6.81147 5.93043 7.00374 5.93043 7.24099Z"
                        fill="#E31E26"
                      />
                    </svg>
                    Email Does Not Exist
                  </span>
                )}
              </div>

              <button
                disabled={!emailValid}
                className={emailValid ? "fg-btn btn-valid" : "fg-btn "}
                onClick={(e) => handleVerificationLink(e)}
              >
                {!loader ? "Send Verification Link" : "Sending..."}
              </button>
            </div>
          </div>
        )}
      </div>
    </Fragment>
  );
}

export default ForgotPassword;
